@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
    --primary-color: #9147ff;
    --navbar-height: 50px;
    --sidebar-width: 240px;
    --shadow-size: 0.4rem;
}

#landing {
    display: grid;
    grid-template-areas: "navbar navbar" "sidebar content";
    grid-template-columns: 240px auto;
    grid-template-columns: var(--sidebar-width) auto;
    grid-template-rows: 50px auto;
    grid-template-rows: var(--navbar-height) auto;
    height: 100vh;
}

.vertical-divider {
    height: 65%;
    border-right: 1px solid rgba(255, 255, 255, 0.1);
}
#content {
    padding: 0px 30px;
    padding-top: 25px;
    background-color: #0e0e10;
    color: white;
    font-family: "Roboto", sans-serif;
}

#content .video-slideshow {
    position: relative;
    height: 300px;
    width: 100%;
}

#content .video-slideshow>.icon-button:first-of-type {
    position: absolute;
    top: 50%;
    left: 50px;
}

#content .video-slideshow>.icon-button:last-of-type {
    position: absolute;
    top: 50%;
    right: 50px;
}

#content .video-slideshow>.video {
    position: absolute;
    left: calc(50% - 380px);
    transition: all 300ms ease-in-out;
}

#content .video-slideshow>.video-left-2 {
    z-index: 1;
    transform: translateX(-65%);
    scale: 0.75;
}

#content .video-slideshow>.video-left-1 {
    z-index: 2;
    transform: translateX(-30%);
    scale: 0.85;
}

#content .video-slideshow>.video-center {
    z-index: 3;
}

#content .video-slideshow>.video-right-1 {
    z-index: 2;
    transform: translateX(30%);
    scale: 0.85;
}

#content .video-slideshow>.video-right-2 {
    z-index: 1;
    transform: translateX(65%);
    scale: 0.75;
}

#content .channels {
    margin-top: 50px;
}

#content .channels>.title {
    font-size: 1.2rem;
    font-weight: 500;
}

#content .channels .videos {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-gap: 1rem;
    gap: 1rem;
    margin-top: 15px;
}

#content .channels .videos .video-bottom video {
    width: 100%;
}
/*GENERAL VIDEO SECTION*/

.video {
    display: flex;
}

.video .side-menu {
    display: grid;
}


/*VIDEO RIGHT SECTION*/

.video-right video {
    width: 520px;
}

.video-right .side-menu {
    grid-template-areas: "profile-picture username" "profile-picture category" "profile-picture watchers" "tags tags" "description description";
    grid-template-columns: 64px auto;
    grid-template-rows: 20px 20px 20px 40px auto;
    padding: 10px;
    width: 220px;
    background-color: #18181b;
}

.video-right .side-menu .profile-picture {
    grid-area: profile-picture;
    width: 48px;
    height: 48px;
    border-radius: 50%;
    object-fit: cover;
}

.video-right .side-menu .username {
    grid-area: username;
    color: var(--primary-color);
    font-weight: 500;
    font-size: 0.85;
}

.video-right .side-menu .username:hover {
    text-decoration: underline;
    cursor: pointer;
}

.video-right .side-menu .category {
    grid-area: category;
    color: var(--primary-color);
    font-size: 0.85rem;
}

.video-right .side-menu .category:hover {
    text-decoration: underline;
    cursor: pointer;
}

.video-right .side-menu .watchers {
    grid-area: watchers;
    font-size: 0.85rem;
}

.video-right .side-menu .tags {
    grid-area: tags;
}

.video-right .side-menu .tags>.button {
    margin: 0px 2px;
}

.video-right .side-menu .description {
    grid-area: description;
    font-size: 0.8rem;
}


/*VIDEO BOTTOM SECTION*/

.video-bottom {
    flex-direction: column;
}

.video-bottom .video-wrapper {
    position: relative;
    margin-bottom: 10px;
}

.video-bottom .video-wrapper:hover {
    cursor: pointer;
}

.video-bottom .video-wrapper .left-bar-side {
    position: absolute;
    top: 0px;
    left: 0px;
    bottom: 0px;
    width: var(--shadow-size);
    transform: scaleX(0);
    background-color: green;
    transition: all 100ms linear;
    transform-origin: 0px 100% 0px;
}

.video-bottom .video-wrapper:hover .left-bar-side {
    transform: scaleX(1);
}

.video-bottom .video-wrapper .left-bar-side::before {
    content: " ";
    position: absolute;
    top: 0px;
    left: 0px;
    border-top: var(--shadow-size) solid transparent;
    border-right: var(--shadow-size) solid green;
    border-bottom: var(--shadow-size) solid transparent;
    transform: translateY(calc(-1 * var(--shadow-size))) scale(0);
    transform-origin: left center 0px;
    transition: all 100ms linear;
}

.video-bottom .video-wrapper:hover .left-bar-side::before {
    transform: translateY(calc(-1 * var(--shadow-size))) scale(1);
}

.video-bottom .video-wrapper .bottom-bar-side {
    position: absolute;
    bottom: 0px;
    left: 0px;
    right: 0px;
    height: var(--shadow-size);
    transform: scaleY(0);
    background-color: green;
    transition: all 100ms linear;
    transform-origin: 0px 100% 0px;
}

.video-bottom .video-wrapper:hover .bottom-bar-side {
    transform: scaleY(1);
}

.video-bottom .video-wrapper .bottom-bar-side::before {
    content: " ";
    position: absolute;
    bottom: 0px;
    right: 0px;
    border-left: var(--shadow-size) solid transparent;
    border-top: var(--shadow-size) solid green;
    border-right: var(--shadow-size) solid transparent;
    transform: translateX(var(--shadow-size)) scale(0);
    transform-origin: center bottom 0px;
    transition: all 100ms linear;
}

.video-bottom .video-wrapper:hover .bottom-bar-side::before {
    transform: translateX(var(--shadow-size)) scale(1);
}

.video-bottom .video-wrapper video {
    transition: all 100ms linear;
}

.video-bottom .video-wrapper:hover video {
    transform: translate(var(--shadow-size), calc(-1 * var(--shadow-size)));
}

.video-bottom .side-menu {
    grid-template-areas: "profile-picture title" "profile-picture username" ". category" ". tags";
    grid-template-columns: 64px auto;
    grid-template-rows: 20px 20px 20px auto;
}

.video-bottom .side-menu .profile-picture {
    grid-area: profile-picture;
    width: 48px;
    height: 48px;
    border-radius: 50%;
    object-position: center;
    object-fit: cover;
}

.video-bottom .side-menu .profile-picture:hover {
    cursor: pointer;
}

.video-bottom .side-menu .title {
    grid-area: title;
    font-size: 0.85rem;
    font-weight: 500;
}

.video-bottom .side-menu .username {
    grid-area: username;
    color: #a1a1a1;
    font-size: 0.85rem;
}

.video-bottom .side-menu .username:hover {
    color: var(--primary-color);
    cursor: pointer;
}

.video-bottom .side-menu .category {
    grid-area: category;
    color: #a1a1a1;
    font-size: 0.85rem;
}

.video-bottom .side-menu .category:hover {
    color: var(--primary-color);
    cursor: pointer;
}

.video-bottom .side-menu .tags {
    grid-area: tags;
}

.video-bottom .side-menu .tags>.button {
    margin: 0px 2px;
    margin-top: 5px;
}

.video-bottom .side-menu .tags>.button:first-of-type {
    margin-right: 2px;
}
.button {
    border-radius: 3px;
    padding: 8px 10px;
    border: none;
    color: white;
    font-family: Roboto;
    font-weight: 500;
    font-size: 0.8rem;
}

.button:hover {
    cursor: pointer;
}

.primary-button {
    background-color: #9147ff;
}

.primary-button:hover {
    background-color: #772ce8;
}

.secondary-button {
    background-color: #3a3a3d;
}

.secondary-button:hover {
    background-color: #464649;
}

.tertiary-button {
    padding: 3px 10px;
    border-radius: 25px;
    background-color: #3a3a3d;
    color: #b5c1c2;
    font-size: 0.75rem;
}

.tertiary-button:hover {
    background-color: #464649;
}
.icon-button {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    background: none;
    border: none;
    border-radius: 4px;
    height: 28px;
    width: 28px;
    color: white;
}

.icon-button:hover {
    background-color: rgba(255, 255, 255, 0.2);
    cursor: pointer;
}

.icon-button .number {
    position: absolute;
    left: 5px;
    bottom: 15px;
    border: 2px solid black;
    background-color: #e91916;
    padding: 1.5px 7px;
    border-radius: 15px;
    font-family: Roboto;
    font-size: 0.8rem;
}

.icon-button:hover .tooltip {
    visibility: visible;
    transition-delay: 0.2s;
}

.icon-button .tooltip {
    position: absolute;
    visibility: hidden;
    border-radius: 4px;
    padding: 5px;
    background-color: white;
    color: black;
    font-family: "Roboto";
    font-weight: 500;
    white-space: nowrap;
    transition: visibility;
}

.icon-button .tooltip::after {
    content: " ";
    position: absolute;
    border-width: 5px;
    border-style: solid;
}

.icon-button .tooltip-bottom {
    top: 150%;
}

.icon-button .tooltip-bottom::after {
    bottom: 100%;
    left: 50%;
    margin-left: -5px;
    border-color: transparent transparent white transparent;
}

.icon-button .tooltip-right {
    left: 150%;
}

.icon-button .tooltip-right::after {
    right: 100%;
    top: 50%;
    margin-top: -5px;
    border-color: transparent white transparent transparent;
}
#account-section {
    display: flex;
    flex: 1 1;
    align-items: center;
    justify-content: flex-end;
    margin-right: 5px;
}

#account-section>* {
    margin: 0px 5px;
}
#navbar {
    display: flex;
    grid-area: navbar;
    background-color: #18181b;
    color: white;
    font-family: 'Roboto', sans-serif;
}
#navigation-section {
    display: flex;
    flex: 1 1;
    align-items: center;
}

#navigation-section .logo {
    display: contents;
}

#navigation-section .logo img {
    height: 100%;
}
.primary-link {
    color: white;
    text-decoration: none;
    font-size: 1.1rem;
    font-weight: 500;
}

.primary-link:hover {
    color: var(--primary-color);
}

.secondary-link {
    color: var(--primary-color);
    text-decoration: none;
    font-size: 1.1rem;
    font-weight: 600;
}

.secondary-link:hover {
    text-decoration: underline;
}

.tertiary-link {
    color: rgba(255, 255, 255, 0.65);
    font-size: 1.1rem;
    text-decoration: none;
}

.tertiary-link:hover {
    color: var(--primary-color);
}
#search-section {
    display: flex;
    align-items: center;
    padding: 15px 0px;
    flex: 1 1;
}

#search-section .search-bar {
    color: #999693;
    background-color: #464649;
    border: 2px solid #464649;
    width: 280px;
    height: 100%;
    padding: 8px;
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
    font-family: Roboto;
    font-weight: 500;
    font-size: 0.9rem;
    outline: none;
}

#search-section .search-bar:hover {
    border: 2px solid #999693;
}

#search-section .search-bar:focus {
    border: 2px solid var(--primary-color);
    background-color: black;
}

#search-section .search-button {
    outline: none;
    display: grid;
    place-content: center;
    background-color: #29292c;
    border: none;
    width: 20px;
    height: 100%;
    padding: 10px;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
    box-sizing: content-box;
}

#search-section .search-button:hover {
    cursor: not-allowed;
}

#search-section .search-button svg {
    color: #464649;
}
#sidebar {
    grid-area: sidebar;
    background-color: #1f1f23;
    color: white;
    font-family: "Roboto", sans-serif;
}

#sidebar .top-section {
    display: flex;
    align-items: center;
    justify-content: space-around;
    margin: 10px 0px;
}

#sidebar .top-section>span {
    text-transform: uppercase;
    font-weight: 500;
    font-size: 0.8rem;
}

#sidebar .twitch-message-section {
    margin: 0px 10px;
    margin-top: 10px;
    padding: 25px;
    border-radius: 8px;
    background-color: #18181b;
    box-shadow: 0px 1px 5px 0px rgba(0, 0, 0, 0.9);
}

#sidebar .twitch-message-section .title {
    margin: 0px;
    font-size: 1.5rem;
    font-weight: 500;
}

#sidebar .twitch-message-section .title>span {
    color: var(--primary-color);
}

#sidebar .twitch-message-section .description {
    margin: 8px 0px;
    line-height: 1.3rem;
    font-size: 0.9rem;
}
.user {
    display: grid;
    grid-template-areas: "profile-picture username watchers" "profile-picture description blank";
    grid-template-columns: 40px auto auto;
    padding: 6px 10px;
}

.user:hover {
    background-color: #26262c;
    cursor: pointer;
}

.user .profile-picture {
    grid-area: profile-picture;
    width: 32px;
    height: 32px;
    border-radius: 50%;
}

.user .username {
    grid-area: username;
    font-size: 0.9rem;
    font-weight: 500;
}

.user .description {
    grid-area: description;
    font-size: 0.75rem;
    color: #a1a1a1;
    /* font-weight: 500; */
    letter-spacing: 1px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.user .watchers {
    grid-area: watchers;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    font-size: 0.9rem;
    color: #c0c0c0;
}

.user .watchers .dot {
    font-size: 0.55rem;
    margin-right: 5px;
}
